.default-nav{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
    padding-top: 10px;
    text-transform: uppercase;
    justify-content: space-around;

     
   ul {
        width: auto;
        height: 18px;
      

    li {
        display: flex;
        list-style: none;
       
      

      a {
        display: flex;
        font-size: clamp(10px, 12px, 18px);
        font-family:"Poppins",serif ;
        font-weight: bolder;
        font-style: normal;
        color: $color-gray;
        text-decoration: none;
        text-wrap: nowrap;
        justify-content: center;
        align-items: center;
        gap: 4px;

        img {
            width: auto;
            height: auto;
            margin-left: 2px;
        
          }

      }
      
      a:hover{
        color: $color-purple-2;
    
        img{
          fill: $color-purple-2;
          color: $color-purple-2;
        }
         
      }
    }
  }
}