.container-banner-card{

    display: flex;
    width: 100%;
    height: 350px;
    border-radius: 16px;
    background-image: linear-gradient(to bottom, #ffffff0c, #000000d8),
    url(../../../../../public/image/banner2.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    align-items: flex-end;

    .banner-card{

        display: flex;
        flex-direction: column;
        width: 100%;
        height: 70%;
        color: $color-white;
        justify-content: flex-end;
        padding-left: 50px;
        padding-bottom: 40px;

        .main-text{

         font-size: clamp(20px, 40px, 60px);
         font-weight: bold;
         font-family: "Poppins", serif;
         
        }

        .info-text {

         width: 180px;
         text-wrap: wrap;
         font-size: clamp(8px, 16px, 32px);
         font-weight: lighter;
         font-family: "Poppins", serif;
         margin-bottom: 32px;

        }

        .btn-checkout {

            display: flex;
            width: 144px;
            height: 48px;
            font-size: clamp(8px, 16px, 32px);
            font-weight: bold;
            background-color: $color-yellow;
            border: none;
            border-radius: 6px;
            color: $color-purple-3;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            text-transform: uppercase;
            
        }

        .btn-checkout:hover {
         color: $color-white;

        }

    }
}