@media (max-width: 1080px) {

    body {

        .container-list-product {
            position: relative;
            width: auto;
            overflow: scroll;

            .list-products {

                width: 100%;
                height: inherit;
                flex-wrap: wrap;
            }

        }

        footer {

            .container-subscribe {

                width: auto;
                font-size: clamp(7px, 2rem, 12px);

                .label-description h2 {

                    font-size: 22px;
                    font-family: "Poppins", serif;
                    font-weight: lighter;

                }

                .label-description p {


                    font-size: 14px;
                    font-family: "Poppins", serif;
                    font-weight: lighter;

                }
            }

            .social {

                font-size: clamp(3px, 5px, 6px);
                display: flex;
                flex-direction: column;
                gap: 26px;


            }
        }
    }
}

@media (max-width: 1160px) {

    body {

        .container-list-product {
            position: relative;
            width: auto;

            .list-products {

                width: 100%;
                height: inherit;
                flex-wrap: wrap;
            }

        }

        .container-card {
            padding: 0;
            width: auto;
            height: auto;

        }


        .list-products .carousel-button:first-child {
            position: absolute;
            left: 50px;
            z-index: 1;

        }

        .list-products .carousel-button:last-child {
            position: absolute;
            right: 50px;
            z-index: 1;

        }

        footer {

            .container-subscribe {

                width: auto;
                font-size: clamp(4px, 2rem, 8px);

                .label-description h2 {

                    font-size: 18px;
                    font-family: "Poppins", serif;
                    font-weight: lighter;

                }

                .label-description p {


                    font-size: 8px;
                    font-family: "Poppins", serif;
                    font-weight: lighter;

                }
            }

            .social {

                font-size: clamp(3px, 5px, 6px);
                display: flex;
                flex-direction: row;
                gap: 26px;

                .social-list {
                    width: 50%;

                }

                .social-list .list-description p {
                    width: 100%;
                    font-size: 10px;
                    margin-left: 2px;
                }
            }
        }
    }
}

@media (max-width: 720px) {

    body {

        .container-list-product {
            position: relative;
            width: auto;
            overflow: scroll;

            .list-products {

                width: 100%;
                height: inherit;
                flex-wrap: wrap;
            }
        }

        .container-list-product {

            position: relative;
            width: auto;

        }

        footer {

            .container-subscribe {

                width: auto;
                font-size: clamp(4px, 2rem, 10px);

                .label-description h2 {

                    font-size: clamp(6px, 2rem, 10px);
                    font-family: "Poppins", serif;
                    font-weight: lighter;

                }

                .label-description p {

                    width: auto;
                    text-align: start;
                    text-wrap: auto;
                    font-size: 10px;
                    font-family: "Poppins", serif;
                    font-weight: lighter;

                }
            }

            .social {

                font-size: clamp(3px, 5px, 6px);
                text-align: start;
                text-wrap: auto;
                display: flex;
                flex-direction: column;
                gap: 26px;

                .social_network_social {

                    width: auto;
                    text-align: start;
                    text-wrap: auto;
                }

                .social-list {

                    display: flex;
                    flex-direction: column;

                }
            }
        }
    }
}